@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,100&display=swap);
body {
  margin: 0;
  font-family: "Piazzolla", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heroImage {
  position: relative;
  background-size: cover;
  height: 90vh;
  width: 98vw;
  padding: 0 50px;
}

.hero {
  display: flex;
  margin: 0 auto;
  justify-content: center;
}
@media only screen and (max-width: 1000px) {
  .heroImage {
    height: 70vh;
  }
}

@media only screen and (max-width: 767px) {
  .heroImage {
    padding: 0 25px;
  }
}

@media only screen and (max-width: 610px) {
  .heroImage {
    height: 50vh;
  }
}
@media only screen and (max-width: 500px) {
  .heroImage {
    padding: 0 10px;
  }
}

.introduction {
  background-size: 100%;
  height: 50vh;
  width: 40vw;
}
.intro-text {
  margin-left: 50px;
}

.row {
  width: 95vw;
}

.picture {
  height: 300px;
  background-position: top;
  background-size: cover;
  height: inherit;
  width: inherit;
  right: 0;
  padding: 0;
}
.picture-container {
  padding: 0;
  margin-right: 50px;
}
#headerText {
  font-size: 3rem;
  font-weight: bolder;
}

.writeUp {
  font-size: 2rem;
}

#fancyText {
  color: red;
  font-weight: bold;
}

@media only screen and (max-width: 1171px) {
  #headerText {
    font-size: 2.5rem;
  }

  .writeUp {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 1000px) {
  #headerText {
    font-size: 2.2rem;
  }

  .writeUp {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 890px) {
  #headerText {
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 767px) {
  .intro-container {
    margin: 0 25px;
  }

  .intro-text {
    margin: auto auto;
  }

  .picture-container {
    margin-bottom: 50px;
    margin-right: 0;
  }

  #headerText {
    font-size: 2.8rem;
  }

  .writeUp {
    font-size: 1.8rem;
  }
}
@media only screen and (max-width: 500px) {
  .intro-container {
    margin: 0 15px;
  }
}

.box {
  padding: 60px;
}

.bold {
  font-size: 2rem;
  font-weight: bold;
}

.card {
  height: 200px;
  width: 300px;
  align-self: center;
  margin: 20px auto;
}

.card-deck {
  margin: 150px auto;
  justify-content: center;
  width: 92vw;
}
.card-icons {
  display: flex;
  margin: 0 auto;
  height: 100px;
  /* justify-content: center; */
}

.card-title {
  font-family: sans-serif;
  font-weight: bold;
}

.carousel {
  padding: 50px;
}

.slide-img {
  height: 500px;
  cursor: pointer;
}

@media only screen and (max-width: 767px) {
  .carousel {
    padding: 0 25px;
  }
}

@media only screen and (max-width: 500px) {
  .carousel {
    padding: 0 10px;
  }
}

.chart {
  width: 50vw;
  height: 70vh;
  display: flex;
  align-self: flex-start;
  cursor: pointer;
  margin-left: 60px;
}
.heading {
  display: flex;
  padding: 50px;
}
.hero {
  margin-bottom: 160px;
}

.loading {
  padding: 50px;
}

@media only screen and (max-width: 900px) {
  .hero {
    flex-direction: column;
  }
  .chart {
    margin: 0 auto;
  }
  .heading {
    justify-content: center;
  }
}

.navbar {
  background-color: rgb(206, 233, 255);
}

.nav-link {
  padding: 0;
  font-size: 1.5rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.nav-item {
  padding-left: 10px;
  padding-right: 10px;
}

.logo {
  font-family: "Piazzolla", serif;
}

.header {
  margin-bottom: 60px;
}

.school {
  cursor: pointer;
}
.school:hover {
  color: blue;
}

table {
  border-collapse: collapse;
  width: 100%;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.footer {
  background-color: rgb(41, 39, 39);
  padding: 100px 50px;
  font-family: "Piazzolla", serif;
  margin-top: 50px;
}

.footer-links {
  text-decoration: none;
  color: white;
  font-family: "Piazzolla", serif;
}
.footer-links:hover {
  text-decoration: none;
  color: rgb(160, 150, 150);
}

.copyright {
  height: 30px;
  background-color: rgb(31, 29, 29);
  font-family: sans-serif;
  padding-top: 5px;
}
@media only screen and (max-width: 767px) {
  .contact-container {
    margin-bottom: 40px;
  }
}

@media only screen and (max-width: 576px) {
  .social-container {
    margin-bottom: 40px;
  }
}

.student {
  cursor: pointer;
}
.student:hover {
  color: blue;
}

.collegePage {
  padding: 30px;
}

.title {
  font-size: 3rem;
  font-weight: bolder;
}

.bold {
  font-size: 2rem;
  font-weight: bold;
}



